import * as types from './types'
import { SIGNOUT_SUCCESS } from '../users/types'

const initialState = {
  transcripts: null,
  files: []
}

const zoomData = (state = initialState, action) => {
  switch (action.type) {
    case types.GET_TRANSCRIPTS_SUCCESS:
      return {...state, transcripts: action.data}
    
    case SIGNOUT_SUCCESS:
      return initialState

    default:
      return state
  }
}

export default zoomData
