import * as types from './types'
import { apiUrl } from 'config'

export const getTranscripts = () => {
  return (dispatch) => {
    fetch(apiUrl.GET_TRANSCRIPTS, {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json',
      }
    })
      .then(response => response.json())
      .then(data => {
        if (!data.error) {
          dispatch(getTranscriptsSuccess(data.scriptData));
        } else {
          console.log(data.errorObj)
        }
      })
      .catch(error => {
        console.log(error)
      })
  }
}

export const getTranscriptsSuccess = (data) => ({
  type: types.GET_TRANSCRIPTS_SUCCESS,
  data
})

export const getAccessTokenError = error => ({
  type: types.GET_ACCESS_TOKEN_ERROR,
  error
})

